//
//-- Footer
//

.page-footer {
  background: #fc4f1e;
  padding: em(80) 1.5em;
  margin-left: 55px;
}

.page-footer__title {
  font-size: em(19);
  font-weight: bold;
  color: #FFF;
  text-align: center;
  text-transform: capitalize;

  @include tablet-port {
    font-size: em(23)
  };

  @include tablet-land {
    font-size: em(27)
  };
}

.page-footer__content {
  margin: 0 auto;
  text-align: center;
}

.page-footer__item {
  display: inline-block;
  font-size: em(20);
  font-weight: bold;
  color: #000;
  // padding-left: 1em;
  padding-right: 1em;
  text-transform: capitalize;
  margin-top: .7em;

  @include tablet-port {
    font-size: em(25)
  };

  @include tablet-land {
    font-size: em(35)
  };
}

.page-footer__copyright {
  font-size: em(14);
  font-weight: 300;
  color: #FFF;
  text-align: center;
  margin-top: 1.5em;
}
