//
//-- Header
//

.page-header {
  position: fixed;
  top: 0;
  left: 55px;
  line-height: 55px;
  height: 55px;
  width: 100vh;
  z-index: 15;
  font-weight: normal;
  -webkit-transform: rotate(90deg);
  -webkit-transform-origin: top left;
  -moz-transform-origin: top left;
  transform-origin: top left;
}

.page-nav {
  background: $color-base;
  color: #FFF;
  text-align: center;
  padding: 0 1.25em;
  position: absolute;
}

.page-nav__item {
  background: #000;
  float: left;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  font-size: em(11);
  padding: 0 1.25em;

  @include tablet-land {
    font-size: em(13);
  };
}
