//-- Title
//
.title-small {
  position: fixed;
  top: 15px;
  right: 15px;
  height: 30px;
  z-index: 99;
}

.title-small__title {
  font-size: rem(25);
  text-transform: uppercase;
  font-weight: 600;
  color: #000;
  line-height: 1;
  text-align: center;
}

.title-small__title-complement {
  font-size: rem(22);
  display: block;
  font-weight: 300;
}
