//
//-- Bio
//

.bio__item {
  padding: 20px 20px 40px 20px;
  position: relative;

  &:hover {
    .bio__item-title {
      color: #fc4f1e;
    }
  }
  //
  // @include tablet-port {
  //   padding: 20px 20px 40px 20px;
  // };
  //
  // @include wide {
  //   padding: 20px 20px 40px 20px;
  // };
}

.bio__item-date {
  position: absolute;
  left: 5em;
  text-align: right;
  color: #fc4f1e;
  font-weight: normal;
  -webkit-transform: translate(-270%, 20%);
  font-size: em(13);

  @include tablet-port {
    font-size: em(14);
    -webkit-transform: translate(-280%, 40%);
  };

  @include tablet-land {
    font-size: em(16);
    -webkit-transform: translate(-300%, 80%);
  };
}

.bio__item-title {
  text-align: left;
  text-transform: capitalize;
  color: $color-base;
  font-weight: 900;
  font-size: em(40);

  @include tablet-port {
    font-size: em(60)
  };

  @include tablet-land {
    font-size: em(80);
  };
}

.bio__item-excerpt {
  text-align: left;
  text-transform: capitalize;
  color: #666;
  font-weight: 400;
  font-size: em(17);

  @include tablet-port {
    font-size: em(20);
  };

  @include tablet-land {
    font-size: em(25);
  };
}
